import { JaaSMeeting, JitsiMeeting } from '@jitsi/react-sdk';
import React, { useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';

const VideoCall = () => {
    const { t } = useTranslation();
    const apiRef = useRef();
    const [ showNew, toggleShowNew ] = useState(false);
    const [ knockingParticipants, updateKnockingParticipants ] = useState([]);

    const printEventOutput = payload => {};

    const handleAudioStatusChange = (payload, feature) => {};

    const handleChatUpdates = payload => {
        if (payload.isOpen || !payload.unreadCount) return;
        apiRef.current.executeCommand('toggleChat');
    };

    const handleKnockingParticipant = payload => {
        updateKnockingParticipants(participants => [ ...participants, payload?.participant ])
    };

    const resolveKnockingParticipants = condition => {
        knockingParticipants.forEach(participant => {
            apiRef.current.executeCommand('answerKnockingParticipant', participant?.id, condition(participant));
            updateKnockingParticipants(participants => participants.filter(item => item.id === participant.id));
        });
    };

    const handleJitsiIFrameRef1 = iframeRef => {
        iframeRef.style.border = '10px solid #3d3d3d';
        iframeRef.style.background = '#3d3d3d';
        iframeRef.style.height = '400px';
        iframeRef.style.marginBottom = '20px';
    };

    const handleJitsiIFrameRef2 = iframeRef => {
        iframeRef.style.marginTop = '10px';
        iframeRef.style.border = '10px dashed #df486f';
        iframeRef.style.padding = '5px';
        iframeRef.style.height = '400px';
    };

    const handleJaaSIFrameRef = iframeRef => {
        iframeRef.style.border = '10px solid #3d3d3d';
        iframeRef.style.background = '#3d3d3d';
        iframeRef.style.height = '400px';
        iframeRef.style.marginBottom = '20px';
    };

    const handleApiReady = apiObj => {
        apiRef.current = apiObj;
        apiRef.current.on('knockingParticipant', handleKnockingParticipant);
        apiRef.current.on('audioMuteStatusChanged', payload => handleAudioStatusChange(payload, 'audio'));
        apiRef.current.on('videoMuteStatusChanged', payload => handleAudioStatusChange(payload, 'video'));
        apiRef.current.on('raiseHandUpdated', printEventOutput);
        apiRef.current.on('titleViewChanged', printEventOutput);
        apiRef.current.on('chatUpdated', handleChatUpdates);
        apiRef.current.on('knockingParticipant', handleKnockingParticipant);
    };

    const handleReadyToClose = () => {
        /* eslint-disable-next-line no-alert */
        alert('videoCall.Ready to close...');
    };

    const generateRoomName = () => `JitsiMeetRoomNo${Math.random() * 100}-${Date.now()}`;

    // Multiple instances demo
    const renderNewInstance = () => {
        if (!showNew) {
            return null;
        }

        return (
            <JitsiMeeting
                roomName = { generateRoomName() }
                getIFrameRef = { handleJitsiIFrameRef2 } />
        );
    };

    const renderButtons = () => (
        <div style={{ margin: '15px 0', display: 'flex', justifyContent: 'center' }}>
            <button
                type='button'
                title={t('videoCall.raiseHandCommand')}
                style={{ border: 0, borderRadius: '6px', fontSize: '14px', background: '#f8ae1a', color: '#040404', padding: '12px 46px', margin: '2px' }}
                onClick={() => apiRef.current.executeCommand('toggleRaiseHand')}
            >
                {t('videoCall.raiseHand')}
            </button>
            <button
                type='button'
                title={t('videoCall.resolveParticipantCommand')}
                style={{ border: 0, borderRadius: '6px', fontSize: '14px', background: '#0056E0', color: 'white', padding: '12px 46px', margin: '2px' }}
                onClick={() => resolveKnockingParticipants(({ name }) => !name.includes('test'))}
            >
                {t('videoCall.resolveKnockingParticipants')}
            </button>
            <button
                type='button'
                title={t('videoCall.subjectCommand')}
                style={{ border: 0, borderRadius: '6px', fontSize: '14px', background: '#df486f', color: 'white', padding: '12px 46px', margin: '2px' }}
                onClick={() => apiRef.current.executeCommand('subject', 'New Subject')}
            >
                {t('videoCall.changeSubject')}
            </button>
            <button
                type='button'
                title={t('videoCall.newInstanceCommand')}
                style={{ border: 0, borderRadius: '6px', fontSize: '14px', background: '#3D3D3D', color: 'white', padding: '12px 46px', margin: '2px' }}
                onClick={() => toggleShowNew(!showNew)}
            >
                {t('videoCall.newInstance')}
            </button>
        </div>
    );

    const renderSpinner = () => (
        <div style={{ fontFamily: 'sans-serif', textAlign: 'center' }}>
            {t('videoCall.loading')}
        </div>
    );

    return (
        <>
            <h1 style={{ fontFamily: 'sans-serif', textAlign: 'center' }}>
                {t('videoCall.title')}
            </h1>
            <JitsiMeeting
                roomName={generateRoomName()}
                spinner={renderSpinner}
                configOverwrite={{ subject: t('videoCall.conferenceSubject'), hideConferenceSubject: false }}
                lang={t.language}
                onApiReady={handleApiReady}
                onReadyToClose={handleReadyToClose}
                getIFrameRef={handleJitsiIFrameRef1}
            />
            <JaaSMeeting
                roomName={generateRoomName()}
                lang={t.language}
                useStaging={true}
                getIFrameRef={handleJaaSIFrameRef}
            />
            {renderButtons()}
            {renderNewInstance()}
        </>
    );
};

export default VideoCall;
