import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
// import axios from 'axios';
import dummyFatwas from '../services/dummyFatwas';

const MyQuestions = () => {
    const [questions, setQuestions] = useState([]);
    const [filteredQuestions, setFilteredQuestions] = useState([]);
    const { token, userId } = useSelector((state) => state.auth);
    const { t } = useTranslation();

    useEffect(() => {
        // Fetch questions from the API
        const fetchQuestions = async () => {
            try {
                if (token) {
                    // Fetch questions from the API
                    // const response = await axios.get('/api/my-questions', {
                    //     headers: {
                    //         'Authorization': `Bearer ${token}`
                    //     }
                    // });

                    // Filter questions based on user ID
                    // const userQuestions = response.data.filter(fatwa => fatwa.userId === userId);

                    const userQuestions = dummyFatwas.filter(fatwa => fatwa.userId === userId);

                    setQuestions(userQuestions);
                } else {
                    // Handle case where there is no token
                    console.error('No authentication token found.');
                    // Optionally, redirect to login or show an error message
                }
            } catch (error) {
                console.error('Error fetching questions:', error);
                // Handle error, e.g., show a user-friendly message or redirect
            }
        };

        fetchQuestions();

    }, [token, userId]);

    useEffect(() => {
        setFilteredQuestions(
            questions.map(fatwa => ({
                ...fatwa,
                status: fatwa.isAnswered ? t('myQuestions.answered') : t('myQuestions.unanswered')
            }))
        );
    }, [questions, t]);

    return (
        <div className="my-questions-container p-8" style={{ direction: 'rtl' }}>
            <h1 className="my-questions-title text-2xl font-bold mb-4">{t('myQuestions.title')}</h1>
            <div className="my-questions-list space-y-4">
                {filteredQuestions.map((question, index) => (
                    <div className="my-questions-item bg-white p-4 rounded-md shadow-md mb-4" key={index}>
                        <div className="my-questions-header">
                            <p className="my-questions-item-title"><strong>{question.title}</strong></p>
                            <div className="my-questions-item-info text-gray-600">
                                <span>{t('myQuestions.refNo')}: {question.refNo} | {t('myQuestions.status')}: {question.status}</span>
                            </div>
                        </div>
                        <p>{question.question}</p>
                        <Link to={`/fatwa/${question.refNo}`} className="text-color-green hover:underline">
                            {t('myQuestions.readMore')}
                        </Link>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default MyQuestions;
