import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
// import axios from 'axios';
import dummyFatwas from '../services/dummyFatwas';
import { useTranslation } from 'react-i18next';

const FatwaEncyclopedia = () => {
  const [fatwas, setFatwas] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredFatwas, setFilteredFatwas] = useState([]);
  const location = useLocation();
  const { t } = useTranslation();

  useEffect(() => {
    // Fetch fatwas from the API
    // axios.get('/api/fatwas')
    //   .then(response => setFatwas(response.data))
    //   .catch(error => console.error('Error fetching fatwas:', error));
    
    // Dummy data for fatwas
    setFatwas(dummyFatwas);

    // Get the search term from the URL query parameter
    const params = new URLSearchParams(location.search);
    const searchQuery = params.get('search');
    if (searchQuery) {
        setSearchTerm(searchQuery);
    }
  }, [location]);

  useEffect(() => {
    setFilteredFatwas(
      fatwas.filter(fatwa =>
        (
            fatwa.title.toLowerCase().includes(searchTerm.toLowerCase())
            ||
            fatwa.question.toLowerCase().includes(searchTerm.toLowerCase())
        )
      )
    );
  }, [searchTerm, fatwas]);

  return (
    <div className="encyclopedia-container p-8" style={{ direction: 'rtl' }}>
      <h1 className="encyclopedia-title text-2xl font-bold mb-4">{t('fatwaEncyclopedia.title')}</h1>
      <input
        type="text"
        className="encyclopedia-search border rounded p-2 mb-4 w-full"
        placeholder={t('fatwaEncyclopedia.searchPlaceholder')}
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        style={{ textAlign: 'right' }}
      />
      <div className="encyclopedia-list space-y-4">
        {filteredFatwas.map((fatwa, index) => (
          <div className="encyclopedia-item bg-white p-4 rounded-md shadow-md mb-4" key={index}>
            <div className="encyclopedia-header">
              <p className="encyclopedia-item-title"><strong>{fatwa.title}</strong></p>
              <div className="encyclopedia-item-info text-gray-600">
                <span>{fatwa.date} | {t('fatwaEncyclopedia.refNo')}: {fatwa.refNo}</span>
              </div>
            </div>
            <p>{fatwa.question}</p>
            <Link to={`/fatwa/${fatwa.refNo}`} className="text-color-green hover:underline">
              {t('fatwaEncyclopedia.readMore')}
            </Link>
          </div>
        ))}
      </div>
    </div>
  );
}

export default FatwaEncyclopedia;
