import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { updateUser, deleteUser } from '../redux/actions/authActions'; // Assuming these actions exist
import { useTranslation } from 'react-i18next';

const Profile = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const user = useSelector((state) => state.auth.user);
  const { t } = useTranslation();

  if (!user) {
    console.log('User not found in Redux store');
  }

  const [email, setEmail] = useState(user.email || '');
  const [username, setUsername] = useState(user.username || '');
  const [password, setPassword] = useState('');

  const handleUpdate = () => {
    // Dispatch the update action with the new details
    dispatch(updateUser({ email, username, password }));
  };

  const handleDeleteAccount = () => {
    if (window.confirm(t('profile.confirmDelete'))) {
      dispatch(deleteUser());
      // Redirect to home page after deletion
      navigate('/');
    }
  };

  return (
    <div className="profile-container max-w-xl mx-auto py-10 px-20 rounded-lg shadow-lg mt-10">
        <div className="background-image-right-bottom-corner"></div>
        <h2 className="profile-title">{t('profile.title')}</h2>
        <div className="input-group">
            <input
                type="text"
                placeholder={t('profile.usernamePlaceholder')}
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
            />
            <span className="icon"><i className="fas fa-user"></i></span>
        </div>
        <div className="input-group">
            <input
                type="email"
                placeholder={t('profile.emailPlaceholder')}
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
            />
            <span className="icon"><i className="fas fa-envelope"></i></span>
        </div>
        <div className="input-group">
            <input
                type="password"
                placeholder={t('profile.passwordPlaceholder')}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
            />
            <span className="icon"><i className="fas fa-key"></i></span>
        </div>
        <button
            onClick={handleUpdate}
            className="w-full bg-gradient-green-r text-button-white py-2 px-4 mb-2 rounded-md hover:bg-green-600 transition"
        >
            {t('profile.updateButton')}
        </button>
        <button
            onClick={handleDeleteAccount}
            className="w-full bg-gradient-red-r text-button-white py-2 px-4 rounded-md hover:bg-red transition"
        >
            {t('profile.deleteButton')}
        </button>
    </div>
  );
};

export default Profile;
