import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

const FatwaChat = () => {
    const { t } = useTranslation();
    const [isChatVisible, setIsChatVisible] = useState(false);
    const [chatMessages, setChatMessages] = useState([]);
    const [currentChatMessage, setCurrentChatMessage] = useState('');

    // Toggles the visibility of the chat popup
    const toggleChatVisibility = () => {
        setIsChatVisible(!isChatVisible);
    };
    // Handles changes to the chat input field
    const handleChatInputChange = (event) => {
        setCurrentChatMessage(event.target.value);
    };

    // Handles the Enter key press to send a message
    const handleChatInputKeyUp = (event) => {
        if (event.key === 'Enter') {
        const trimmedMessage = currentChatMessage.trim();
        if (!trimmedMessage) return;

        // Add user message to chat
        setChatMessages(prevMessages => [
            ...prevMessages,
            { sender: 'user', text: trimmedMessage }
        ]);

        // Clear input field
        setCurrentChatMessage('');

        // Simulate a reply
        setTimeout(() => {
            setChatMessages(prevMessages => [
            ...prevMessages,
            { sender: 'bot', text: "Hello, how can I help you today?" }
            ]);
        }, 500);
        }
    };

    return (
        <div id="chat-widget-container">
            <div
                id="chat-bubble"
                onClick={toggleChatVisibility}
                className="w-16 h-16 bg-white rounded-full flex items-center justify-center cursor-pointer text-3xl shadow-lg"
            >
                <svg xmlns="http://www.w3.org/2000/svg" className="w-10 h-10 text-lime-700" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="2">
                    <path strokeLinecap="round" strokeLinejoin="round" d="M8 10h.01M12 10h.01M16 10h.01M9 16H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-5l-5 5v-5z" />
                </svg>
            </div>
            {isChatVisible && (
                <div id="chat-popup" className="absolute bottom-20 right-0 w-96 bg-white rounded-md shadow-md flex flex-col transition-all text-sm">
                    <div id="chat-header" className="flex justify-center items-center p-4 bg-gradient-green-r text-white rounded-t-md">
                        <h3 className="m-0 text-lg">{t('chat.directChat')}</h3>
                    </div>
                    <div id="chat-messages" className="flex-1 p-4 overflow-y-auto">
                        {chatMessages.map((message, index) => (
                            <div key={index} className={`flex mb-3  ${message.sender === 'user' ? 'justify-end' : 'justify-start'}`}>
                                <div className={`rounded-lg py-2 px-4 max-w-[70%] ${message.sender === 'user' ? 'bg-gradient-green-r text-white' : 'bg-zinc-200 text-black'}`}>
                                    {message.text}
                                </div>
                            </div>
                        ))}
                    </div>
                    <div id="chat-input-container" className="p-4 border-t border-gray-200">
                        <div className="flex items-center">
                            <input
                                type="text"
                                id="chat-input"
                                value={currentChatMessage}
                                onChange={handleChatInputChange}
                                onKeyUp={handleChatInputKeyUp}
                                className="flex-1 border border-gray-300 rounded-md px-4 py-2 outline-none text-right bg-zinc-200 text-black"
                                placeholder={t('chat.writeYourQuestion')}
                            />
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default FatwaChat;
