import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import dummyFatwas from '../services/dummyFatwas';
import { useTranslation } from 'react-i18next';

const FatwaDetail = () => {
  const { refNo } = useParams();
  const navigate = useNavigate();
  const [fatwa, setFatwa] = useState(null);
  const { t } = useTranslation();

  useEffect(() => {
    // Fetch the Fatwa based on refNo (replace this with actual API call later)
    const selectedFatwa = dummyFatwas.find(fatwa => fatwa.refNo === refNo);
    setFatwa(selectedFatwa);
  }, [refNo]);

  if (!fatwa) {
    return <div className="text-center text-gray-500">{t('fatwaDetail.loading')}</div>;
  }

  const handleBackClick = () => {
    navigate('/encyclopedia'); // Navigate back to the Fatwa Encyclopedia
  };

  return (
    <div className="max-w-3xl mx-auto p-6">
      <div className="background-image-right-bottom-corner"></div>
      <div className="fatwa-question mb-4">
        <h2 className="text-2xl font-bold mb-4 text-center">{fatwa.title}</h2>
        <p className="text-gray-700 mb-2 text-right">
          <strong>{t('fatwaDetail.questionLabel')} :</strong>
        </p>
        <p className="text-gray-700 text-right">{fatwa.question}</p>
      </div>
      <div className="fatwa-answer mb-4">
        <p className="text-gray-700 mb-2 text-right">
          <strong>{t('fatwaDetail.answerLabel')} :</strong>
        </p>
        <p className="text-gray-700 text-right">{fatwa.answer}</p>
      </div>
      <div className="text-center mt-6">
        <button
          className="bg-gradient-green-r text-button-white font-bold py-2 px-4 rounded hover:bg-blue-600"
          onClick={handleBackClick}
        >
          {t('fatwaDetail.backButton')}
        </button>
      </div>
    </div>
  );
};

export default FatwaDetail;
