import React, { useState, useEffect } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { logoutUser } from '../redux/actions/authActions';
import { useTranslation } from 'react-i18next';
import LanguageSwitcher from './LanguageSwitcher';
import dummyNotifications from '../services/dummyNotifications';


const Navbar = () => {
  // State to manage notification dropdown visibility
  const [isNotificationDropdownOpen, setIsNotificationDropdownOpen] = useState(false);
  // State to manage profile dropdown visibility
  const [isProfileDropdownOpen, setIsProfileDropdownOpen] = useState(false);

  const [notifications, setNotifications] = useState([]);

  const navigate = useNavigate();
  const currentLocation = useLocation();
  const dispatch = useDispatch();
  const { t } = useTranslation();

  // Get token and user from Redux state
  const { token, user } = useSelector((state) => state.auth);
  const isLoggedIn = !!token;

  useEffect(() => {
    // This is where the Socket.IO connection would be established and notifications would be received
    // For now, we'll use dummy data
    setNotifications(dummyNotifications);

    // const socket = io('http://socket-server-url');
    // socket.on('notification', (notification) => {
    //   setNotifications((prevNotifications) => [...prevNotifications, notification]);
    // });

    // Cleanup socket connection on component unmount
    // return () => socket.disconnect();
  }, []);

  const handleNotificationClick = (notification) => {
    if (!notification.read) {
      const updatedNotifications = notifications.map((notif) =>
        notif.id === notification.id ? { ...notif, read: true } : notif
      );
      setNotifications(updatedNotifications);
    }
  };

  const unreadCount = notifications.filter((notif) => !notif.read).length;

  const handleLogout = () => {
    dispatch(logoutUser());
    setIsProfileDropdownOpen(false);
    // Redirect to login page
    navigate('/login');
  };

  const isLoginPage = currentLocation.pathname === '/login';

  return (
    <nav className="navbar bg-gradient-green-l">
        <div className="navbar-container">
            <div className="logo">
                <Link to="/"><img src="../assets/icons/logo.ico" alt="FATWA" /></Link>
            </div>
            <ul className="navbar-menu">
                <li className="navbar-item">
                    <Link
                        to="/"
                        className={`navbar-link ${currentLocation.pathname === '/' ? 'navbar-link-active-link' : ''}`}
                    >{t('navbar.home')}</Link>
                </li>
                <li className="navbar-item">
                    <Link
                        to="/about"
                        className={`navbar-link ${currentLocation.pathname === '/about' ? 'navbar-link-active-link' : ''}`}
                    >{t('navbar.about')}</Link>
                </li>
                <li className="navbar-item">
                    <Link
                        to="/donate"
                        className={`navbar-link ${currentLocation.pathname === '/donate' ? 'navbar-link-active-link' : ''}`}
                    >{t('navbar.donate')}</Link>
                </li>
                {isLoggedIn && (
                    <li className="navbar-item">
                        <Link
                            to="/my-questions"
                            className={`navbar-link ${currentLocation.pathname === '/my-questions' ? 'navbar-link-active-link' : ''}`}
                        >{t('navbar.myQuestions')}</Link>
                    </li>
                )}
            </ul>
            <div className="navbar-user">
                <LanguageSwitcher />
                {isLoggedIn ? (
                    <>
                        <div
                            className="notifications-wrapper"
                            onMouseEnter={() => setIsNotificationDropdownOpen(true)}
                            onMouseLeave={() => setIsNotificationDropdownOpen(false)}
                        >
                            <span className="notifications-button navbar-link">
                                <span className="icon-with-badge">
                                    <i className="fas fa-bell"></i>
                                    {unreadCount > 0 && <span className="notifications-badge">{unreadCount}</span>}
                                </span>
                                {t('navbar.notifications')}
                            </span>
                            {isNotificationDropdownOpen && (
                                <div className="notifications-dropdown">
                                    {notifications.length > 0 ? (
                                        notifications.map((notification) => (
                                            <a
                                                key={notification.id}
                                                href={notification.url || '#'}
                                                className={`notifications-dropdown-item ${notification.read ? 'read' : 'unread'}`}
                                                onClick={() => handleNotificationClick(notification)}
                                            >
                                                <span className="notification-status">
                                                    {notification.read ? '✓' : '•'}
                                                </span>
                                                {notification.message}
                                            </a>
                                        ))
                                    ) : (
                                        <div className="notifications-dropdown-item">
                                            {t('navbar.noNotifications')}
                                        </div>
                                    )}
                                </div>
                            )}
                        </div>
                        <div
                            className="profile-wrapper"
                            onMouseEnter={() => setIsProfileDropdownOpen(true)}
                            onMouseLeave={() => setIsProfileDropdownOpen(false)}
                        >
                            <span className="profile-button navbar-link">
                                <i className="fas fa-user"></i> {user.username}
                            </span>
                            {isProfileDropdownOpen && (
                                <div className="profile-dropdown flex flex-col justify-start items-start">
                                    <Link
                                        to="/profile"
                                        className="profile-dropdown-item"
                                        onClick={() => setIsProfileDropdownOpen(false)}
                                    >
                                        <i className="fas fa-user-circle"></i>
                                        <span>{t('navbar.profile')}</span>
                                    </Link>
                                    <button onClick={handleLogout} className="profile-dropdown-item">
                                        <i className="fas fa-sign-out-alt"></i>
                                        <span>{t('navbar.logout')}</span>
                                    </button>
                                </div>
                            )}
                        </div>
                    </>
                ) : (
                    <>
                        <Link
                            to="/login"
                            className={`navbar-user-navbar-btn ${isLoginPage ? 'navbar-user-navbar-btn-active' : 'navbar-user-navbar-btn-inactive'}`}
                        >{t('navbar.login')}</Link>
                        <Link
                            to="/signup"
                            className={`navbar-user-navbar-btn ${isLoginPage ? 'navbar-user-navbar-btn-inactive' : 'navbar-user-navbar-btn-active'}`}
                        >{t('navbar.signup')}</Link>
                    </>
                )}
            </div>
        </div>
    </nav>
);

};

export default Navbar;
