import Cookies from 'js-cookie';
import { SET_USER_DATA, SET_AUTH_TOKEN } from './types';

export const setAuthToken = (token, userId) => {
  return {
    type: SET_AUTH_TOKEN,
    payload: { token, userId },
  };
};

export const setUserData = (user) => {
  return {
    type: SET_USER_DATA,
    payload: { user },
  };
};

export const loginUser = (username, password, rememberMe) => async (dispatch) => {
    try {
      // Call for odoo API to authenticate user

      // Dummy response for test
      const response = {
        token: 'DummyToken',
        userId: username,
        user: {
          id: username,
          username: username,
          email: username + '@example.com',
        },
      };
  
      if (response.token) {
        const { token, userId, user } = response;
  
        // Store in cookies based on rememberMe
        if (rememberMe) {
          Cookies.set('authToken', token, { expires: 30 }); // 30 days
          Cookies.set('userId', userId, { expires: 30 });
        } else {
          Cookies.set('authToken', token);
          Cookies.set('userId', userId);
        }
        Cookies.set('user', JSON.stringify(user));
  
        dispatch(setAuthToken(token, userId));
        dispatch(setUserData(user));
      }
    } catch (error) {
      console.error("Login failed", error);
    }
  };

export const logoutUser = () => (dispatch) => {
  Cookies.remove('authToken');
  Cookies.remove('userId');
  Cookies.remove('user');

  dispatch({ type: 'LOGOUT' });
};

export const initializeUserData = () => {

    return (dispatch) => {
    const token = Cookies.get('authToken');
    const userId = Cookies.get('userId');

    if (token && userId) {
        dispatch(setAuthToken(token, userId));

        const user = JSON.parse(Cookies.get('user'));
        if (user) {
          dispatch(setUserData(user));
        }
    }
  };
};

export const updateUser = (updatedUserData) => async (dispatch) => {
  try {

    const user = JSON.parse(Cookies.get('user'));
    console.log("User before update:", user);
    if (updatedUserData.username) {
      user.username = updatedUserData.username;
    }
    if (updatedUserData.email) {
      user.email = updatedUserData.email;
    }
    if (updatedUserData.password) {
      user.password = updatedUserData.password;
    }

    // Update the user data in cookies
    Cookies.set('user', JSON.stringify(user));

    console.log("User updated successfully", user);
    // Update the Redux store
    dispatch(setUserData(user));

  } catch (error) {
    console.error("Update user failed", error);
  }
};

export const deleteUser = () => async (dispatch) => {
  try {
    
    // Clear user data from cookies
    Cookies.remove('authToken');
    Cookies.remove('userId');
    Cookies.remove('user');

    // Dispatch logout action to clear Redux state
    dispatch({ type: 'LOGOUT' });
  } catch (error) {
    console.error("Delete user failed", error);
  }
};
