import { SET_AUTH_TOKEN, SET_USER_DATA } from '../actions/types';

const initialState = {
    token: null,
    userId: null,
    user: null,
  };
  
  const authReducer = (state = initialState, action) => {
    switch (action.type) {
      case SET_AUTH_TOKEN:
        return {
          ...state,
          token: action.payload.token,
          userId: action.payload.userId,
        };
      case SET_USER_DATA:
        return {
          ...state,
          user: action.payload.user,
        };
      case 'LOGOUT':
        return initialState;
      default:
        return state;
    }
  };
  
  export default authReducer;
  