import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { loginUser } from '../redux/actions/authActions';
import { useTranslation } from 'react-i18next';

import '../styles/main.css';

const Login = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [rememberMe, setRememberMe] = useState(false);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { t } = useTranslation();

    const handleLogin = (e) => {
        // Prevent the default form submission
        e.preventDefault();

        // Dispatch the login action
        dispatch(loginUser(username, password, rememberMe));

        // Redirect to home page after successful login
        navigate('/');
    };

    return (
        <div className="login-container">
            <div className="background-image-left-top-corner"></div>
            <div className="login-box">
                <h2 className="login-title">{t('login.title')}</h2>
                <form onSubmit={handleLogin}>
                    <div className="input-group">
                        <input
                            type="text"
                            placeholder={t('login.usernamePlaceholder')}
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            required
                        />
                        <span className="icon"><i className="fas fa-user"></i></span>
                    </div>
                    <div className="input-group">
                        <input
                            type="password"
                            placeholder={t('login.passwordPlaceholder')}
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            required
                        />
                        <span className="icon"><i className="fas fa-lock"></i></span>
                    </div>
                    <div className="remember-me">
                        <label>
                            <input
                                type="checkbox"
                                checked={rememberMe}
                                onChange={() => setRememberMe(!rememberMe)}
                            />
                            {t('login.rememberMe')}
                        </label>
                    </div>
                    <button type="submit" className="btn btn-primary bg-gradient-green-r text-button-white">
                        {t('login.submit')}
                    </button>
                </form>
                <p className="create-account text-color-green">
                    <Link to="/signup">{t('login.createAccount')}</Link>
                </p>
            </div>
        </div>
    );
};

export default Login;
