import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import dummyFatwas from '../services/dummyFatwas';
import '../styles/main.css'; // Import global styles
import FatwaChat from '../components/FatwaChat';

const Home = () => {
    const [fatwas, setFatwas] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [categories, setCategories] = useState([]);
    const [showDropdown, setShowDropdown] = useState(false);
    const [suggestions, setSuggestions] = useState([]); // State for autocomplete suggestions
    const [showSuggestions, setShowSuggestions] = useState(false); // State for suggestion visibility
    const navigate = useNavigate();
    const { t } = useTranslation();

    // Commented out API call section for later use
    /*
    useEffect(() => {
        const fetchFatwas = async () => {
            try {
                const response = await apiService.get('/your-endpoint'); // Replace with your actual endpoint
                setFatwas(response.data);
            } catch (error) {
                console.error('Error fetching fatwas:', error);
            }
        };

        fetchFatwas();
    }, []);
    */

    // Fetch categories from API (dummy implementation for now)
    const fetchCategories = () => {
    // Uncomment this when the actual API is available
    /*
    fetch('API_ENDPOINT')
      .then(response => response.json())
      .then(data => setCategories(data.categories))
      .catch(error => console.error('Error fetching categories:', error));
    */
    
        // Using dummyFatwas to get unique categories for now
        const uniqueCategories = [...new Set(dummyFatwas.map(fatwa => fatwa.category))];
        setCategories(uniqueCategories);
    };

    // Dummy data for now
    useEffect(() => {
        
        // Sort Fatwas by date (most recent first)
        const sortedFatwas = dummyFatwas.sort((a, b) => new Date(b.date) - new Date(a.date));
        
        // Select the first three Fatwas
        const latestThreeFatwas = sortedFatwas.slice(0, 3);

        // Set the state with the latest three Fatwas
        setFatwas(latestThreeFatwas);
    }, []);

    useEffect(() => {
        // Update suggestions based on search term
        if (searchTerm) {
            const filteredSuggestions = dummyFatwas
                .filter(fatwa => fatwa.title.toLowerCase().includes(searchTerm.toLowerCase()))
                .map(fatwa => fatwa.title);
            setSuggestions(filteredSuggestions);
            setShowSuggestions(true);
        } else {
            setShowSuggestions(false);
        }
    }, [searchTerm]);

    const handleSubmitQuestionButtonClick = () => {
        navigate('/submit-question');
    };
    
    const handleSearchInputKeyPress = (event) => {
        if (event.key === 'Enter') {
            // Redirect to FatwaEncyclopedia with the search term as a query parameter
            navigate(`/encyclopedia?search=${searchTerm}`);
        }
    };
    
    const handleCategoryClick = (category) => {
        navigate(`/encyclopedia?category=${category}`);
    };
    
    const toggleDropdown = () => {
        if (!showDropdown) {
          fetchCategories(); // Fetch categories when the dropdown is opened
        }
        setShowDropdown(!showDropdown);
    };

    

    // Handles suggestion click
    const handleSuggestionClick = (suggestion) => {
        setSearchTerm(suggestion);
        setShowSuggestions(false);
        navigate(`/encyclopedia?search=${suggestion}`);
    };

    const handleVideoCallButtonClick = () => {
        navigate('/video-call');
    };

    return (
        <div className="home-page">
            <header className="header bg-gradient-green-l text-white text-center py-10">
                <h1 className="text-3xl">{t('home.header.quote')}</h1>
                <div className="search-bar mt-6 relative">
                    <input
                        type="text"
                        placeholder={t('home.search.placeholder')}
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                        onKeyUp={handleSearchInputKeyPress}
                        className="input w-full max-w-lg px-4 py-3 rounded-md border-none text-gray-800"
                    />
                    {showSuggestions && (
                        <div className="suggestions-dropdown absolute w-[43%] text-black mt-12 rounded-md shadow-lg z-50">
                            {suggestions.length > 0 ? (
                                suggestions.map((suggestion, index) => {
                                    const parts = suggestion.split(new RegExp(`(${searchTerm})`, 'gi'));
    
                                    return (
                                        <button
                                            key={index}
                                            onClick={() => handleSuggestionClick(suggestion)}
                                            className="block w-full text-right px-4 pt-3 hover:bg-gray-200 rounded-md"
                                        >
                                            <div className="border-b border-gray-300 mx-2 pb-2">
                                                {parts.map((part, i) => (
                                                    <span
                                                        key={i}
                                                        className={
                                                            part.toLowerCase() === searchTerm.toLowerCase()
                                                                ? "text-green-700 font-semibold"
                                                                : ""
                                                        }
                                                    >
                                                        {part}
                                                    </span>
                                                ))}
                                            </div>
                                        </button>
                                    );
                                })
                            ) : (
                                <div className="no-suggestions px-4 pt-3 pb-2 rounded-md">
                                    {t('home.search.noSuggestions')}
                                </div>
                            )}
                        </div>
                    )}
                </div>
            </header>
            <section className="fatwa-actions">
                <button
                    className="fatwa-action bg-gradient-green-r text-button-white py-2 px-6 rounded-lg"
                    onClick={handleVideoCallButtonClick}
                >
                    {t('home.fatwaActions.bookCall')}
                </button>
                <button
                    className="fatwa-action bg-gradient-green-r text-button-white py-2 px-6 rounded-lg"
                    onClick={handleSubmitQuestionButtonClick}
                >
                    {t('home.fatwaActions.askQuestion')}
                </button>
                <div
                    className="fatwa-action bg-gradient-green-r text-button-white relative flex justify-center items-start hover:cursor-pointer"
                    onClick={toggleDropdown}
                >
                    <button className="text-white items-center">
                        {t('home.fatwaActions.category')}
                    </button>
                    {showDropdown && (
                        <div className="categories-dropdown absolute right-0 mt-2 w-full rounded-md shadow-lg ring-1 ring-black ring-opacity-5">
                            <div className="py-1">
                                {categories.map((category) => (
                                    <button
                                        key={category}
                                        onClick={() => handleCategoryClick(category)}
                                        className="block hover:bg-gray-200"
                                    >
                                        {category}
                                    </button>
                                ))}
                            </div>
                        </div>
                    )}
                </div>
            </section>
    
            <div className="flex justify-between px-4">
                <section className="recent-fatwas flex-1">
                    {fatwas.map((fatwa, index) => (
                        <div className="fatwa-item bg-white p-4 rounded-md shadow-md mb-4" key={index}>
                            <div className="fatwa-header">
                                <p className="fatwa-title">
                                    <strong>{fatwa.title}</strong>
                                </p>
                                <div className="fatwa-info">
                                    <span className="text-gray-600">
                                        {fatwa.date} | {t('home.fatwaItem.refNo')} {fatwa.refNo}
                                    </span>
                                </div>
                            </div>
                            <p>{fatwa.question}</p>
                            <Link to={`/fatwa/${fatwa.refNo}`} className="text-color-green hover:underline">
                                {t('home.fatwaItem.readMore')}
                            </Link>
                        </div>
                    ))}
                </section>
                <section className="categories w-1/4">
                    {/* The dropdown menu is now part of the above section */}
                </section>
            </div>
            <FatwaChat />
        </div>
    );
    
    
    
};

export default Home;
