import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const Signup = () => {
  const { t } = useTranslation();
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');

  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();

    // Simple validation
    if (password !== confirmPassword) {
      setError(t('signup.passwordMismatch'));
      return;
    }

    // Perform the signup API call here
    // Example: apiService.signup({ username, email, password })

    // Redirect on success
    navigate('/login');
  };

  return (
    <div className="signup-container">
        <div className="background-image-left-top-corner"></div>
        <div className="signup-box">
            <h2 className="signup-title">{t('signup.title')}</h2>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="input-group">
                  <input
                      type="text"
                      placeholder={t('signup.usernamePlaceholder')}
                      value={username}
                      onChange={(e) => setUsername(e.target.value)}
                      required
                  />
                  <span className="icon"><i className="fas fa-user"></i></span>
              </div>
              <div className="input-group">
                <input
                    type="email"
                    placeholder={t('signup.emailPlaceholder')}
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                />
                <span className="icon"><i className="fas fa-envelope"></i></span>
              </div>
              <div className="input-group">
                <input
                    type="password"
                    placeholder={t('signup.passwordPlaceholder')}
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                />
                <span className="icon"><i className="fas fa-key"></i></span>
              </div>
              <div className="input-group">
                <input
                    type="password"
                    placeholder={t('signup.confirmPasswordPlaceholder')}
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    required
                />
                <span className="icon"><i className="fas fa-key"></i></span>
              </div>
              {error && <p className="text-red-500 text-center">{error}</p>}
              <button type="submit" className="w-full bg-gradient-green-r text-button-white py-2 px-4 rounded-md hover:bg-green-600 transition">
                {t('signup.submitButton')}
              </button>
            </form>
        </div>
    </div>
  );
};

export default Signup;
