import React, { useState } from 'react';
// import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { ReactComponent as MessageSentIcon } from '../assets/icons/message-sent.svg';

const SubmitQuestion = () => {
  const { t } = useTranslation();
  const [formData, setFormData] = useState({
    email: '',
    name: '',
    title: '',
    question: ''
  });

  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(false);
  const [refNo, setRefNo] = useState(null);
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleBackClick = () => {
    navigate('/'); // Navigate back to Home page
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null);

    // Validate inputs
    if (!formData.email || !formData.name || !formData.title || !formData.question) {
      setError(t('submitQuestion.allFieldsRequired'));
      return;
    }

    try {
      // const response = await axios.post('https://odoo-api-endpoint/submit-question', formData);
      // setRefNo(response.data.refNo);

      // Temporary generated refNo (for testing purposes)
      const tempRefNo = `FATWA-${Math.floor(100000 + Math.random() * 900000)}`;
      setRefNo(tempRefNo);

      setSuccess(true);
    } catch (error) {
      setError(t('submitQuestion.submissionError'));
    }
  };

  if (success) {
    return (
      <div>
        <div className="background-image-right-bottom-corner"></div>
        <div className="max-w-lg mx-auto bg-white bg-opacity-50 p-16 rounded-lg shadow-lg mt-10 text-center">
          <MessageSentIcon className="mx-auto mb-4 w-13 h-12" />
          <h2 className="text-3xl font-bold text-green-600 mb-4">{t('submitQuestion.successTitle')}</h2>
          <p className="text-gray-700 mb-2">{t('submitQuestion.successMessage')}</p>
          <p className="text-gray-700 mb-6">{t('submitQuestion.refNo')}: {refNo}</p>
          <button
            className="bg-gradient-green-r text-button-white font-bold py-2 px-6 rounded hover:bg-blue-600"
            onClick={handleBackClick}
          >
            {t('submitQuestion.backToHome')}
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="submit-question-container max-w-xl mx-auto py-10 px-20 rounded-lg shadow-lg mt-10">
      <div className="background-image-right-bottom-corner"></div>
      <h2 className="text-2xl font-bold text-gray-800 mb-6 text-center">{t('submitQuestion.title')}</h2>
      {error && <p className="text-red-500 mb-4 text-right">{error}</p>}
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block text-gray-700 text-right">{t('submitQuestion.titleLabel')}:</label>
          <input
            type="text"
            name="title"
            value={formData.title}
            onChange={handleChange}
            className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500 bg-gray-100"
          />
        </div>
        <div>
          <label className="block text-gray-700 text-right">{t('submitQuestion.nameLabel')}:</label>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500 bg-gray-100"
          />
        </div>
        <div>
          <label className="block text-gray-700 text-right">{t('submitQuestion.emailLabel')}:</label>
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500 bg-gray-100"
          />
        </div>
        <div>
          <label className="block text-gray-700 text-right">{t('submitQuestion.questionLabel')}:</label>
          <textarea
            name="question"
            value={formData.question}
            onChange={handleChange}
            className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:ring-indigo-500 focus:border-indigo-500 bg-gray-100"
          ></textarea>
        </div>
        <button
          type="submit"
          className="w-full py-2 px-4 bg-gradient-green-r text-button-white font-semibold rounded-md hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-green-500 focus:ring-offset-2"
        >
          {t('submitQuestion.submitButton')}
        </button>
      </form>
    </div>
  );
};

export default SubmitQuestion;
