import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

const LanguageSwitcher = () => {
    const { i18n } = useTranslation();
    const [isDropdownOpen, setIsDropdownOpen] = useState(false);

    const handleLanguageChange = (language) => {
        i18n.changeLanguage(language);
        setIsDropdownOpen(false);
      };
    
      const toggleDropdown = () => {
        setIsDropdownOpen(!isDropdownOpen);
      };

  return (
    <div className="relative inline-block text-left">
      <div>
        <button
          onClick={toggleDropdown}
          className="inline-flex justify-center w-full rounded-2xl  px-4 py-2 bg-dark-green text-sm font-medium text-white hover:text-black focus:outline-none"
        >
          <svg
            className="mr-2 h-5 w-5"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 20 20"
            fill="currentColor"
            aria-hidden="true"
          >
            <path
              fillRule="evenodd"
              d="M10 3a1 1 0 00-.707.293l-5 5a1 1 0 001.414 1.414L10 5.414l4.293 4.293a1 1 0 001.414-1.414l-5-5A1 1 0 0010 3z"
              clipRule="evenodd"
            />
          </svg>
          {i18n.language}
        </button>
      </div>

      {isDropdownOpen && (
        <div className="origin-top-right absolute right-0 mt-2 w-28 rounded-md shadow-lg bg-white bg-opacity-80 ring-1 ring-black ring-opacity-5 focus:outline-none">
            <div className="py-1" role="menu" aria-orientation="vertical" aria-labelledby="options-menu">
            <a
                href="#"
                className={`block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 ${i18n.language === 'ar' ? 'font-bold' : ''}`}
                role="menuitem"
                onClick={() => handleLanguageChange('ar')}
            >
                العربية
            </a>
            <a
                href="#"
                className={`block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 ${i18n.language === 'en' ? 'font-bold' : ''}`}
                role="menuitem"
                onClick={() => handleLanguageChange('en')}
            >
                English
            </a>
            <a
                href="#"
                className={`block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 ${i18n.language === 'fr' ? 'font-bold' : ''}`}
                role="menuitem"
                onClick={() => handleLanguageChange('fr')}
            >
                Français
            </a>
            <a
                href="#"
                className={`block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 ${i18n.language === 'tzm' ? 'font-bold' : ''}`}
                role="menuitem"
                onClick={() => handleLanguageChange('tzm')}
            >
                ⵜⴰⵎⴰⵣⵉⵖⵜ
            </a>
            </div>
        </div>
      
      )}
    </div>
  );
};

export default LanguageSwitcher;
